const config = {
    env: 'prod',
    get apiIpWithPort() {
        return this.env === 'prod' ? '15.206.81.216:5504' : 'localhost:5504'
    },
    get apiBaseurl() {
        return 'http://' + this.apiIpWithPort
    },
    get apiUrl() {
        return this.apiBaseurl + '/admin/';
    }

}

export default config;